.sort-search {
    width:100%;
    height:45px;
    position:fixed;
    top:0px;
    display:flex;
    .search-left{
        width:10%;
        height:100%;
        position: relative;
        .img{
            width: 60%;
            height: 60%;
            position: absolute;
            top: 0px;
            left: 0px;
            right: 0px;
            bottom: 0px;
            margin: auto;
            img{
                width:100%;
                height: 100%;
            }
        }
    }
    .search-middle {
        width:calc(100% - 20%);
        position: relative;
        .input{
            width: 100%;
            height: 68%;
            border-radius: 30px;
            background: #e7e7e7;
            position: absolute;
            top: 0px;
            left: 0px;
            right: 0px;
            bottom: 0px;
            margin:auto;
            display: flex;
            .img{
                width:40px;
                height: 100%;
                img{
                    width: 50%;
                    height: 100%;
                    margin: auto 10px;
                }
            }
            input{
                height: 80%;
                border: 0px;
                background: #e7e7e7;
                margin: auto 0px;
                outline:none;
            }
        }
    }
    .search-right{
        width: 10%;
        height: 100%;
        position: relative;
        .img{
            width: 60%;
            height: 60%;
            position: absolute;
            top:0px;
            left:0px;
            right:0px;
            bottom:0px;
            margin: auto;
            img{
                width: 100%;
                height: 100%;
            }
        }
    }
}